import React from 'react'
import WorkSheets from '../WorkSheets/index'
import { Redirect , Link , Route , Switch ,  BrowserRouter as Router} from 'react-router-dom'
import {
    Layout, Menu,  Icon,
} from 'antd';
import  './index.css'


const { Header } = Layout;


export default class DashBoard extends React.Component {
  render() {
    if(localStorage.getItem('authenticated') === "true"){  
       let match = this.props.match   
       return (
        <Router>   
            <Layout>
                <Header className="header">
                <div className="logo" />
                <Menu
                    theme="dark"
                    mode="horizontal"
                    style={{ lineHeight: '64px' , paddingLeft : '18%' }}
                >
                    <Menu.Item key="1"><Link to={`${match.url}/worksheets`}><Icon type="file-sync" />Manage Worksheets</Link></Menu.Item>
                    <Menu.Item key="2"><Link to={`${match.url}/tests`}><Icon type="reconciliation" />Manage Tests</Link></Menu.Item>
                    <Menu.Item key="3"><Link to={`${match.url}/updates`}><Icon type="sync" />Manage Updates</Link></Menu.Item>
                </Menu>
                </Header>
                <Switch>
                    <Route exact path = {`${match.url}/worksheets`} render = {props => <WorkSheets {...props} />} />
                    <Route exact path = {`${match.url}/tests`} render = {props => <h1>Tests</h1>} />
                    <Route exact path = {`${match.url}/updates`} render = {props => <h1>Updates</h1>} />
                </Switch>
            </Layout>
        </Router>
       )
    }
    else {
        return <Redirect to = {{ pathname : '/login'}} />
    }
  }
}


