import React from 'react'

import {
    Form, Input, Button
} from 'antd';

import { baseurl } from '../../../baseurl'

export default class AddCategory extends React.Component {
    constructor() {
      super();
      this.state = {
        formLayout: 'horizontal',
        english_name : '',
        hindi_name : ''
      };
    }
  
    handleChange = (event) => {
        this.setState({ [event.target.name] : event.target.value })
    }
    
    handleSubmit = () => {
        if(this.state.english_name === ''){
            alert("Category Name In English Is Required")
        }
        else {
            fetch(baseurl+`categories_and_sub_categories/add_category` , {
                method : 'POST',
                headers: {
                    "Content-Type": "application/json",
                },
                body : JSON.stringify(this.state)
            })
            .then((res) => res.json())
            .then((data) => alert(data.message) , this.setState( { english_name : '' , hindi_name : ''}))
            .catch(err => {alert("Server Error . Please Contact To Technical Team"); console.log(err)})
        }
    }

    render() {
      const { formLayout } = this.state;
      const formItemLayout = formLayout === 'horizontal' ? {
        labelCol: { span: 4 },
        wrapperCol: { span: 14 },
      } : null;
      const buttonItemLayout = formLayout === 'horizontal' ? {
        wrapperCol: { span: 14, offset: 4 },
      } : null;
      return (
        <div>
          <center><h1>Add Category </h1>  </center>
          <Form layout={formLayout}>
            <Form.Item
              label="Category Name(English)"
              {...formItemLayout}
            >
            <Input placeholder="Enter Category In English" 
                value = {this.state.english_name}
                onChange = {this.handleChange}
                name = 'english_name'
            />
            </Form.Item>
            <Form.Item
              label="Category Name(Hindi)"
              {...formItemLayout}
              value = {this.state.hindi_name}
              onChange = {this.handleChange}
              name = 'hindi_name'
            >
            <Input placeholder="Enter Category In English" 
              value = {this.state.hindi_name}
              onChange = {this.handleChange}
              name = 'hindi_name'
            />  
            </Form.Item>
            <Form.Item {...buttonItemLayout}>
              <Button type="primary" onClick = {() => this.handleSubmit()}>Submit</Button>
            </Form.Item>
          </Form>
        </div>
      );
    }
  } 
