import React from 'react'
import { baseurl } from '../../../baseurl'
import {
    Form, Input, Button, Select
} from 'antd';


const Option = Select.Option;



export default class AddSubCategory extends React.Component {
    constructor() {
      super();
      this.state = {
        formLayout: 'horizontal',
        english_name : '',
        hindi_name : '',
        all_categories : [],
        category_id : 0
      };
    }
  
    handleChange = (event) => {
        this.setState({ [event.target.name] : event.target.value })
    }

    handleSelectChange = (value) => {
        this.setState({ category_id : value })
    }
    componentDidMount()  {
        fetch(`${baseurl}categories_and_sub_categories/fetch_all_categories`)
        .then(res => res.json())
        .then(data => this.setState({ all_categories : data}))
        .catch(err => console.log(err))
    }


    handleSubmit = () => {
        if(this.state.english_name === ''){
            alert("Category Name In English Is Required")
        } else if(this.state.category_id === 0) {
            alert("Please Choose Category ....")
        }
        else {
            fetch(baseurl+`categories_and_sub_categories/add_sub_category` , {
                method : 'POST',
                headers: {
                    "Content-Type": "application/json",
                },
                body : JSON.stringify(this.state)
            })
            .then((res) => res.json())
            .then((data) => alert(data.message) , this.setState( { english_name : '' , hindi_name : '' , category_id : 0}))
            .catch(err => {alert("Server Error . Please Contact To Technical Team"); console.log(err)})
        }
    }

    render() {
      const { formLayout } = this.state;
      const formItemLayout = formLayout === 'horizontal' ? {
        labelCol: { span: 4 },
        wrapperCol: { span: 14 },
      } : null;
      const buttonItemLayout = formLayout === 'horizontal' ? {
        wrapperCol: { span: 14, offset: 4 },
      } : null;
      return (
        <div>
          <center><h1>Add Sub  Category </h1>  </center>
          <Form layout={formLayout}>
            <Form.Item
              label="Select Category"
              {...formItemLayout}
            >
            <Select
                showSearch
                style={{ width: 200 }}
                placeholder="Select Category "
                optionFilterProp="children"
                onChange={this.handleSelectChange}
                filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
            >
                {this.state.all_categories.map(category => {
                    return(
                        <Option value = {`${category._id}`} key = {category._id}>{category.english_name}</Option>
                    )
                })}
                
            </Select>
            </Form.Item>
            <Form.Item
              label="Sub Category Name(English)"
              {...formItemLayout}
            >
            <Input placeholder="Enter Sub Category In English" 
                value = {this.state.english_name}
                onChange = {this.handleChange}
                name = 'english_name'
            />
            </Form.Item>
            <Form.Item
              label="Sub Category Name(Hindi)"
              {...formItemLayout}
              value = {this.state.hindi_name}
              onChange = {this.handleChange}
              name = 'hindi_name'
            >
            <Input placeholder="Enter Sub  Category In English" 
              value = {this.state.hindi_name}
              onChange = {this.handleChange}
              name = 'hindi_name'
            />  
            </Form.Item>
            <Form.Item {...buttonItemLayout}>
              <Button type="primary" onClick = {() => this.handleSubmit()}>Submit</Button>
            </Form.Item>
          </Form>
        </div>
      );
    }
  } 
